body {
  text-align: center;
  background-color: #d3d3d3;
  margin: 0;
  padding: 20px;
  font-family: Montserrat, sans-serif;
}

h1 {
  color: #000;
}

.search-form {
  margin-bottom: 20px;
  position: relative;
}

.search-form input {
  min-width: 550px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
}

.search-form input:hover {
  border: 1px solid gray;
}

.search-form input:focus {
  background-color: #d3d3d3;
  border-color: rgba(0, 0, 0, 0);
  outline: 1px solid #fff;
}

.search-form button {
  color: gray;
  cursor: pointer;
  background-color: #d3d3d3;
  border: 1px solid gray;
  border-radius: 5px;
  padding: 10px 20px;
  position: relative;
  top: 50%;
  right: 20px;
}

.search-form button:hover, .search-form button:active {
  color: #fff;
  background-color: gray;
}

.load-more {
  color: gray;
  cursor: pointer;
  background-color: #d3d3d3;
  border: 1px solid gray;
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px 20px;
  display: none;
}

.load-more:active, .load-more:hover {
  color: #fff;
  background-color: gray;
}

.gallery {
  max-width: 1366px;
  grid-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  margin: 0 auto;
  padding: 0;
  display: grid;
}

.photo-card {
  border: 1px solid gray;
  margin-bottom: 20px;
  padding: 10px;
}

.photo-card .photo-description {
  justify-content: space-around;
  align-items: center;
  padding: 5px;
  font-size: 20px;
  display: flex;
}

.photo-card .photo-description p {
  margin: 0 5px;
  font-size: .8em;
}

div img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 5px;
}

.info {
  text-align: center;
  border: 1px solid #000;
  border-radius: 5px;
  gap: 10px;
  padding: 15px;
  display: flex;
}

.info-item {
  margin: 5px 0;
}

